import React, { useState } from "react";
import style from "./Package.module.css";
import PHC from "./PHC/PHC";
import HBC from "./HBC/HBC";

const Package = () => {
  const [cal, setCal] = useState(false);
  const [tab, setTab] = useState(1);
  return (
    <div className={style.parent_container}>
      <div className={style.package}>
        <div className={style.tab}>
          <div
            onClick={() => {
              setTab(1);
            }}
            className={tab === 1 ? style["tab--active"] : style["tab_element"]}
          >
            HOME BLOOD COLLECTION
          </div>
          <div
            onClick={() => {
              setTab(2);
            }}
            className={tab === 2 ? style["tab--active"] : style["tab_element"]}
          >
            PREVENTIVE HEALTH CHECKUP
          </div>
        </div>
        <div className={style.subcontainer}>{tab === 1 ? <HBC /> : <PHC />}</div>
      </div>
    </div>
  );
};

export default Package;
